* {
  box-sizing: content-box;
}

html::-webkit-scrollbar{
  display: none;
}

body {
  background-color: rgb(12, 12, 12);
  font-size: 10px;
}

.App {
  color: rgb(204, 204, 204);
  width: 100vw;
}
.App > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-header {
  height: 100vh;
}
.App-header > div {
  font-size: 2.4rem;
  font-weight: bold;
  white-space: pre-line;
  height: 8vh;
  width: 27vw;
}

.App-aboutMe {
  background-color: white;
  height: 40vh;
}

.subTitle {
  position: relative;
  color: black;
  font-size: 3.2rem;
  font-weight: bold;
}
.subTitle::after{
  position: absolute;
  content: "";
  top: 100%;
  height: 0.3rem;
  width: 6rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: rgb(12, 12, 12);
}

.content {
  font-size: 2.0rem;
  color: black;
  width: 40vw;
}

.line {
  content: "";
  height: 8rem;
  width: 0.3rem;
  margin: 0 4rem;
  background-color: black;
  opacity: 0.8;
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}